<script context="module" lang="ts">
    import { Decimal } from "decimal.js"

    export function toAscii(str: string): string {
        return `\`${str}\``;
    }

    function n2d(num: number) {
        return new Decimal(num);
    }

    export function fractionToMath(num: Decimal): [boolean, string, number] {
        let sign = num.isNeg() ? '-' : '';
        let w = num.trunc();
        let n = num.minus(w).abs();

        if (n.eq(0)) {
            let str = num.toNumber().toLocaleString('en');
            return [false, str, str.length];
        }

        let d = n2d(1);
        let len = 1;

        while (!n.toFraction()[1].eq(1)) {
            n = n.mul(10);
            d = d.mul(10);
            len++;
        }

        let math = w.eq(0) ?
            `${sign}${n}/${d}` :
            `${w} ${n}/${d}`;
            
        // console.log(`${num}: '${math}'`);

        return [true, math, math.length]
    }

    export function unitTests() {
        function itWorks() {
            fractionToMath(n2d(3));
            fractionToMath(n2d(-4));
            fractionToMath(n2d(0.1));
            fractionToMath(n2d(0.02));
            fractionToMath(n2d(-0.007));
            fractionToMath(n2d(2.07));
            fractionToMath(n2d(-2.173));
        }

        itWorks();
    }

    // unitTests();

</script>