<script context="module" lang="ts">

    import { Decimal } from "decimal.js"
    import Fraction from "fraction.js"

    import {error} from './Logger.svelte'

    
    export function rand(min: number, max: number) {
        min = Math.ceil(min);
        max = Math.floor(max + 1);
        return Math.floor(Math.random() * (max - min) + min);
    }

    export function randFrom<T>(arr: readonly T[]): T {
        return arr[rand(0, arr.length-1)];
    }

    export function randFromIf<T>(arr: readonly T[], 
        iff: (x: T) => boolean): T 
    {
        let filtered = arr.filter(iff);
        // assert there is any number in filtered array
        return filtered[rand(0, filtered.length-1)];
    }

    export function trueFalse(): boolean {
        return rand(0, 1) == 0;
    }

    export function randSign(): number {
        return rand(0, 1) == 0 ? +1 : -1;
    }

    export function pm(num: number): number {
        return num * (rand(0, 1) == 0 ? +1 : -1);
    }

    export function randId(len: number, charSet?: string) {
        charSet ??= 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        var str = '';
        for (var i = 0; i < len; i++) {
            var indx = Math.floor(Math.random() * charSet.length);
            str += charSet.charAt(indx);
        }
        return str;
    }

    export function shuffle<T>(src: readonly T[]): T[] {
        let dst = [...src];
        for (let i = dst.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [dst[i], dst[j]] = [dst[j], dst[i]];
        }
        return dst;
    }

    let audio = new AudioContext();

    export function beep() {
        var osc = audio.createOscillator();
        var gain = audio.createGain();

        osc.connect(gain);
        gain.connect(audio.destination);

        gain.gain.value = 0.5;
        osc.frequency.value = 440;
        osc.type = "triangle";

        osc.start();

        setTimeout(() => osc.stop(), 50);
    } 

    export function run(task: () => void) {
        setTimeout(task, 0);
    }

    export function delay(ms: number) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    export function range(start: number, end: number) {
        return Array.from({length: (end - start)}, (_, k) => k + start);
    }

    export function safe(func: () => void) {
        try {
            return func();
        }
        catch (err) {
            let name = func.name;
            if (name === "") name = "unknown";
            error(`Error in ${name}`, err);
        }
    }

    export function splitNoEmpty(str: string, delim?: string) {
        return str.split(delim ?? " ").filter(Boolean).map(x => x.trim());
    }

    export function isFraction(d: Decimal) {
        return !d.toFraction()[1].eq(1);
    }

    function n2d(n: number) {
        return new Decimal(n);
    }

    export function d2t(d: Decimal, excludeWhole = true): string {
        let parts = d.toFraction();

        if (parts[1].eq(1))
            return d.toNumber().toLocaleString('en');

        let fr = new Fraction(d.toNumber());

        return fr.toFraction(excludeWhole);
    }

    export function nd2f(n: Decimal, d: Decimal) {
        return new Fraction(n.toNumber(), d.toNumber());
    }

    export function d2f(d: Decimal) {
        return new Fraction(d.toNumber());
    }

    export function f2d(fr: Fraction) {
        return n2d(fr.n).div(fr.d);
    }

    export function allAreUnique<T>(arr: T[]): boolean {
        let set = new Set<string>();
        for (let x of arr) {
            let str = `${x}`;
            if (set.has(str)) return false;
            set.add(str);
        }
        return true;
    }

</script>
