<script context="module" lang="ts">

    type Handler = (args: any) => void;

    type One<State> = keyof(State);
    type Many<State> = keyof(State) | ((keyof(State))[]);

    interface Config<State, Event> {
        exclusiveStates: One<State>[][];
        on?: {
            enter?: Many<State>;
            exit?: Many<State>;
            from?: One<State>;
            to?: One<State>;
            action: Handler;
        }[];
        onEnter?: {
            [Name in keyof(State)]?: Handler;
        }
        onExit?: {
            [Name in keyof(State)]?: Handler;
        }
        onTransit?: {
            [Name in keyof(State)]?: {
                [Name in keyof(State)]?: Handler;
            };
        }
        transitions: {
            [Name in keyof Event]?: ({
                from: Many<State>;
                to: One<State>;
            } | {
                [Name in keyof State]?: keyof(State)
            })[]
        }[]
    }

    export interface IFSM<State, Event> {
        configure<State, Event>(config: Config<State, Event>);
        fire<Name extends keyof(Event)>(name: Name, args?: Event[Name]);
    }

    class FSM<State, Event> implements IFSM<State, Event> {
        configure<State, Event>(config: Config<State, Event>) {

        }

        fire<Name extends keyof(Event)>(name: Name, args?: Event[Name]) {

        }
    }

    export function newFsm<State, Event>(config: Config<State, Event>): IFSM<State, Event> {
        let fsm = new FSM<State, Event>();
        fsm.configure(config);
        return fsm;
    }

</script>