<script context="module" lang="ts">

    import { Decimal } from "decimal.js"

    import { Anki } from "../core/Anki.svelte"
    import { pm, rand, randFrom, shuffle, randFromIf, d2t, allAreUnique, nd2f, f2d, d2f } from "../core/Helpers.svelte";
    import { newFsm } from "../core/FSM.svelte";
    import type { IFSM } from "../core/FSM.svelte"

    import type { Round, GameState, GameEvent, Config, GameType } from "./Model.svelte"

    interface IGame {
        readonly fsm: IFSM<GameState, GameEvent>;
        isLast(round: Round): boolean;
        addRounds(count: number);
        nextRound(): Round;
    }

    let _config = Anki.getConfig<Config>();
    let _patterns: string[];
    let _next: number;
    let _totalRounds = _config.rounds;
    let _gameType: GameType = "equ+-";
    let _a: number[] = [10, 90];
    let _b: number[] = [10, 90];
    let _c: number[] = [10, 90];
    let _d: number[] = [10, 90];
    let _aA: Decimal[] = [];
    let _bA: Decimal[] = [];
    let _cA: Decimal[] = [];
    let _dA: Decimal[] = [];
    let _x: string[] = ['X'];
    let _noOverflow: boolean = false;
    let _properOnly: boolean = false;
    let _seconds: number = 15 * _config.rounds;
    let _mathScale: number;

    let n2d = (n: number) => new Decimal(n);
    let d2n = (d: Decimal) => d.toNumber();

    let _fsm = newFsm<GameState, GameEvent>({
        exclusiveStates: [
            ["start", "clicked", "answered"],
            ["right", "wrong"]
        ],
        on: [],
        onEnter: {
        },
        onTransit: {
        },
        transitions: [
        ]
    });

    function parseDecimal(str: string): Decimal {
        let parts = str.split('/');

        if (parts.length === 1)
            return new Decimal(str);

        let [n, d] = [
            new Decimal(parts[0]), 
            new Decimal(parts[1])
        ];

        let fr = n.div(d);

        return fr;
    }

    function getAb(): Decimal[] {
        let a = _aA.length > 0 ?
            randFrom(_aA) :
            n2d(rand(_a[0], _a[1]));

        let b = _bA.length > 0 ?
            randFrom(_bA) :
            n2d(rand(_b[0], _b[1]));

        return [a, b];
    }

    function get(n: number[], nA: Decimal[]): Decimal {
        return nA.length > 0 ?
            randFrom(nA) :
            n2d(rand(n[0], n[1]));
    }

    function percentsSimple(gameType: string, equ: string) {
        return gameType.includes("%") &&
            equ.includes("*");
    }

    function percentsPlus(gameType: string, equ: string) {
        return gameType.includes("%") &&
            equ.includes("+");
    }

    function percentsMinus(gameType: string, equ: string) {
        return gameType.includes("%") &&
            equ.includes("-");
    }

    function newAbc(pattern: string): Decimal[] {

        let tzero = n => n - n % 10;

        if (_gameType === "equ+-") {
            let [a, b] = getAb();

            let a0 = rand(1, 9);
            let b0 = _noOverflow ? rand(0, 9 - a0) : rand(10 - a0, 9);
            let aa = n2d(tzero(a) + a0);
            let bb = n2d(tzero(b) + b0);
            let c  = aa.plus(bb);

            return [
                ...shuffle([aa, bb]), 
                c
            ];
        }

        if (_gameType === "equ*/") {
            let [a, b] = getAb();

            let c = a.mul(b);

            return [
                ...shuffle([a, b]), 
                c
            ];
        }

        if (_gameType === "ab+c=d") {
            let [a, b] = getAb();
            let d = get(_d, _dA);

            let c = d.minus(a.mul(b));

            return [
                ...shuffle([a, b]), 
                c, d
            ];
        }

        if (_gameType === "a*/(b+c)=d") {
            for (let loopLimit = 100; true; )
            {

                let [a, b] = getAb();
                let c = get(_c, _cA);
                let d: Decimal;

                if (pattern.includes('+')) {
                    d = a.mul(b).plus(a.mul(c));
                } else {
                    if (b.lt(c))
                        [b, c] = [c, b];
                    d = a.mul(b).minus(a.mul(c));
                }

                if (pattern.includes('/') && d.eq(0) && loopLimit-- > 0)
                    continue;

                if (pattern.includes('/'))
                    [a, d] = [d, a];

                return [a, b, c, d];
            }
        }

        if (percentsSimple(_gameType, pattern)) {
            let b = get(_b, _bA);
            let c: Decimal;
            let a: Decimal;
            let loopLimit = 100;
            do {
                c = get(_c, _cA);
                a = c.mul(100).div(b);
                if (--loopLimit === 0) break;
            } while (!a.trunc().eq(a));

            if (loopLimit === 0)
                console.log([a, b, c].map(d => d2n(d)));

            return [a, b, c];
        }
        else
        if (percentsPlus(_gameType, pattern)) {
            // assert _bA is here
            let b = randFromIf(_bA, x => x.gte(100));
            let c: Decimal;
            let a: Decimal;

            let loopLimit = 100;
            do {
                c = get(_c, _cA);
                a = c.mul(100).div(b);
                if (--loopLimit === 0) break;
            } while (!a.trunc().eq(a));

            b = b.minus(100);

            if (loopLimit === 0)
                console.log([a, b, c].map(d => d2n(d)));

            return [a, b, c];
        }
        else
        if (percentsMinus(_gameType, pattern)) {
            // assert _bA is here
            let b = randFromIf(_bA, x => x.lt(100));
            let c: Decimal;
            let a: Decimal;

            let loopLimit = 100;
            do {
                c = get(_c, _cA);
                a = c.mul(100).div(b);
                if (--loopLimit === 0) break;
            } while (!a.trunc().eq(a));

            if (loopLimit === 0)
                console.log([a, b, c].map(d => d2n(d)));

            b = n2d(100).minus(b);

            return [a, b, c];
        }

        if (_gameType === "%-a-of-c") {
            let b = get(_b, _bA);
            let c: Decimal;
            let a: Decimal;
            let loopLimit = 100;
            do {
                c = get(_c, _cA);
                a = b.mul(c).div(100);
                if (--loopLimit === 0) break;
            } while (!a.trunc().eq(a));

            if (loopLimit === 0)
                console.log([a, b, c].map(d => d2n(d)));

            return [a, b, c];
        }

        if (_gameType === "fr+-") {
            let [a, b] = getAb();
            let c = a.plus(b);

            return [
                ...shuffle([a, b]), 
                c
            ];
        }

        if (_gameType === "fr*/") {
            let [a, b] = shuffle(getAb());

            let c = pattern.includes('*') ?
                a.mul(b) : a.div(b);

            return [a, b, c];
        }

        if (_gameType === "fr+-ab") {
            let [a, b] = getAb();

            if (a.eq(b)) {
                return pattern.includes('+') ?
                    [a, b, a.plus(b)] :
                    [a.plus(b), ...shuffle([b, a])];
            }

            if (pattern.includes('+')) {
                return [...shuffle([a, b]), a.plus(b)];
            } else {
                let lhs = Decimal.max(a, b);
                let rhs = Decimal.min(a, b);
                let c = lhs.minus(rhs);
                return [lhs, rhs, c];
            }
        }

        if (_gameType === "fr-reduction") {
            for (let loopLimit = 20; true; ) {
                let [a, b] = getAb();
                let d = get(_d, _dA);
                a = a.mul(d);
                b = b.mul(d);
                let fr = nd2f(a, b);
                let c = f2d(fr);

                if ((a.eq(b) || (a.gt(b) && _properOnly)) && loopLimit-- > 0)
                    continue;

                return [a, b, c];
            }
        }

        throw(`Unknown game type '${_gameType}'`);
    }

    function makeAnswers(abc: Decimal[], rightAns: Decimal): Decimal[] {
        if (_gameType === "equ+-") {
            return shuffle([
                rightAns,
                rightAns.plus(pm(10)),
                rightAns.plus(pm(rand(1, 1))),
                rightAns.plus(pm(rand(2, 2)))
            ]);
        }

        if (_gameType === "equ*/") {
            return shuffle([
                rightAns,
                rightAns.plus(pm(rand(1, 1))),
                rightAns.plus(pm(rand(2, 2))),
                rightAns.plus(pm(rand(3, 3)))
            ]);
        }

        if (_gameType === "ab+c=d" || _gameType === "a*/(b+c)=d") {
            return shuffle([
                rightAns,
                rightAns.plus(pm(rand(1, 1))),
                rightAns.plus(pm(rand(2, 2))),
                rightAns.plus(pm(rand(3, 3)))
            ]);
        }

        if (_gameType.includes("%")) {
            return shuffle([
                rightAns,
                rightAns.plus(pm(5)),
                rightAns.plus(pm(10)),
                rightAns.plus(pm(15))
            ]);
        }

        if (_gameType.includes('fr+-') || _gameType === "fr*/") {
            let variate = (x: Decimal, v: Decimal) => 
                x.gt(v) ? x.plus(v.mul(pm(1))) : x.plus(v);

            for (let loopLimit = 100; true; )
            {
                let answers = [
                    rightAns,
                    variate(rightAns, abc[0]),
                    variate(rightAns, abc[1]),
                    variate(rightAns, abc[2])
                ];

                if (!allAreUnique(answers) && loopLimit-- > 0)
                    continue;

                return shuffle(answers);
            }
        }

        if (_gameType === "fr-reduction") {
            let va = (x: number, v: number) => 
                x > v ? x + pm(v) : x + v;

            let fr = d2f(rightAns);

            for (let loopLimit = 100; true; )
            {
                let n1 = fr.n;
                let n2 = va(fr.n, 1);
                let d1 = fr.d;
                let d2 = va(fr.d, 1);

                let answers = [
                    rightAns,
                    n2d(n2).div(d1),
                    n2d(n1).div(d2),
                    n2d(n2).div(d2)
                ];

                if (!allAreUnique(answers) && loopLimit-- > 0)
                    continue;

                return shuffle(answers);
            }
        }

        throw(`Unknown game type '${_gameType}'`);
    }

    function makeEqu(pattern: string, abc: Decimal[]): [string, Decimal] {

        let vars = pattern.match(/\b[A-Z]\b/g);     // single upcase letters
        let v = randFrom(vars);

        let equ = pattern;

        [
            ["_r", "color(red)"],
            ["_g", "color(green)"],
            ["_b", "color(blue)"]
        ].forEach(g => equ = equ.replace(g[0], g[1]))

        equ = equ
            .replace(v, '$')
            .replace("xx", '@')

        let ans = abc[v.charCodeAt(0) - 'A'.charCodeAt(0)];

        if (_gameType == "ab+c=d") {
            let c = abc[2];
            if (c.lt(0)) {
                abc[2] = c.neg();
                equ = equ.replace('+', "-");
            }
            if (ans.lt(0)) 
                ans = ans.neg();
        }

        equ = equ
            .toLocaleLowerCase()
            .replace(/\ba\b/, d2t(abc[0]))
            .replace(/\bb\b/, d2t(abc[1]))
            .replace(/\bc\b/, d2t(abc[2]))
            .replace(/\bd\b/, d2t(abc[3]))
            .replace('@', "xx")
            .replace('$', randFrom(_x));

        return [equ, ans];
    }

    class CGame implements IGame {
        constructor() {
            for (let cmd of Anki.commands()) {
                switch (cmd[0].toLocaleLowerCase()) {
                    case "equ+-":       _gameType = "equ+-";     break;
                    case "equ*/":       _gameType = "equ*/";     break;
                    case "ab+c=d":      _gameType = "ab+c=d";    break;
                    case "%-simple":    _gameType = "%-simple";  break;
                    case "%-plus":      _gameType = "%-plus";    break;
                    case "%-minus":     _gameType = "%-minus";   break;
                    case "%-all":       _gameType = "%-all";     break;
                    case "game":        _gameType = <GameType> cmd[1];
                    case "no-overflow": _noOverflow = false;     break;
                    case "proper-only": _properOnly = false;     break;
                    case "timer":       _seconds = parseInt(cmd[1]);      break;
                    case "m-scale":     _mathScale = parseFloat(cmd[1]); break;
                    case "x":           _x = cmd.slice(1);       break;
                    case "a": _a = cmd.slice(1).map(x => parseInt(x)); break;
                    case "b": _b = cmd.slice(1).map(x => parseInt(x)); break;
                    case "c": _c = cmd.slice(1).map(x => parseInt(x)); break;
                    case "d": _d = cmd.slice(1).map(x => parseInt(x)); break;
                    case "a[]": _aA = cmd.slice(1).map(x => parseDecimal(x)); break;
                    case "b[]": _bA = cmd.slice(1).map(x => parseDecimal(x)); break;
                    case "c[]": _cA = cmd.slice(1).map(x => parseDecimal(x)); break;
                    case "d[]": _dA = cmd.slice(1).map(x => parseDecimal(x)); break;
                }
            }
            _patterns = Anki.tokens();
            _next = -1;
        }

        get fsm() {
            return _fsm;
        }

        isLast() {
            return _next + 1 >= _totalRounds;
        }

        addRounds(count: number) {
            _totalRounds += count;
        }

        nextRound(): Round {
            _next++;

            let gameType = _gameType;

            let pattern = _patterns[_next % _patterns.length];
            let abc = newAbc(pattern);

            abc.push(n2d(0));       // d

            let [equ, rightAns] = makeEqu(pattern, abc);

            let answers = makeAnswers(abc, rightAns);

            let progress = [
                _next < _totalRounds ? _next : _totalRounds,
                _totalRounds
            ];

            return {
                seconds: _seconds,
                gameType,
                progress,
                equ,
                mathScale: _mathScale,
                rightAns,
                answers
            };
        }
    }

    export function newEquation(): IGame {
        return new CGame();
    }

</script>